@charset "utf-8";

/* Ïä§ÏôÄÏù¥Ìçº - Ï†úÌíà Î∞∞ÎÑà Ïª¥Ìè¨ÎÑåÌä∏ CSS - swiper-product.css */



section.swipe_banner {
    width: 100%;
    height: 100%;
}

.swiper-product {
    width: 100%;
    height: 100%;

    /* Í∏ÅÌûàÎäîÍ±∞ ÎßâÍ∏∞ */
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

/* Í∞Å Ïä¨ÎùºÏù¥Îìú (li) */
.swiper-slide.gin {
    position: relative;
    /* Î∂ÄÎ™®ÏûêÍ≤© - ÌÉÄÏù¥ÌãÄÏùò Í≤âÎ∞ïÏä§ */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background: transparent;
    text-align: center;

    cursor: pointer;

    /* aspect-ratio: 583/1440; */

    /* border: 3px solid hotpink; */
}


/* Ï†úÌíà Ïù¥ÎØ∏ÏßÄ */
.swiper-slide.gin img {
    margin: 0 auto;
    display: block;
    width: 75%;
    height: 75%;
    object-fit: cover;

    /* border: 2px dashed red; */
}
/* ÌÉÄÏù¥ÌãÄ Í≤âÎ∞ïÏä§ */
.swiper-slide.gin .wrap:has(.product_tit.gin){
    position: absolute;
    /* Î∂ÄÎ™® - Í∞Å li */
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 0;
    opacity: 1;

    overflow: hidden;
}

/* Ï†úÌíà ÌÉÄÏù¥ÌãÄ */
.swiper-slide.gin .product_tit.gin{
    /* position: absolute; */

    font-family: 'Cormorant Garamond', serif;
    font-size: 7vmax;
    line-height: 1;
    white-space: nowrap;
    font-weight: bold;
    color: var(--ft-navy-color);
    text-shadow: 1px 1px 1px #000;

    /* outline: 1px solid red; */
}
