/* 1. ÎßàÏßÑ,Ìå®Îî© ÏóÜÏï†Í∏∞ */
html,body,h1,h2,h3,h4,h5,h6,ul,ol,dt,dd,p,figure{
    margin: 0;
    padding: 0;
}
/* Ìè∞Ìä∏ ÏÇ¨Ïù¥Ï¶à Î£®Ìä∏ */
body{
    overflow-x: hidden;
    /* Í∞ÄÎ°úÏä§ÌÅ¨Î°§ ÏÉùÍπÄ Î∞©ÏßÄ!(ÌäπÌûà Î™®Î∞îÏùº) */
    font-size: 10px;

