}
/* 2. aÏöîÏÜå Î¶¨ÏÖã */
a{
    color: var(--black-color);
    /* Í∏ÄÏûêÏÉâ */
    text-decoration: none;
    /* Î∞ëÏ§ÑÏóÜÏï∞ */
}
/* 3. ul,ol Î¶¨ÏÖã */
ul,ol{
    list-style: none;
    /* Î∏îÎ¶ø/Ïà´Ïûê Ï†úÍ±∞ */
}
/* 5. Ïù¥ÎØ∏ÏßÄ Î¶¨ÏÖã */
img {
    border: none;
    /* Ïù¥ÎØ∏ÏßÄ ÎßÅÌÅ¨Ïãú ÏÑúÎ≤ÑÏóêÏÑú Î≥ºÎïå ÌååÎûÄÏÉâ ÏûêÎèôÎ≥¥Îçî ÏóÜÏï†Í∏∞ */
    vertical-align: bottom;
    /* Î∞ïÏä§ Ïù¥ÎØ∏ÏßÄ ÌïòÎã®Í∞ÑÍ∑π ÏóÜÏï†Í∏∞ */
}

