    width: 100%;
    height: 80px;
    top: 20px;
    z-index: 2;
}
/* ÏÉÅÎã®ÏòÅÏó≠ ÌîåÎ†âÏä§ Î∞ïÏä§ */
.flx_container{
    max-width: 1400px;
    height: 80px;
    margin: 0 auto;
    padding: 0 30px;

    display: flex;
    justify-content: space-between;
    align-items: center;

    /* border: 1px solid #000; */
}


/* ÏÉÅÎã® Î°úÍ≥† Ïù¥ÎØ∏ÏßÄ ÏöîÏÜå */
.tlogo img{
    width: 150px;
}

/* ÌñÑÎ≤ÑÍ±∞ ÏïÑÏù¥ÏΩò (Íµ¨Í∏ÄÏã¨Î≥º) ÏÑ∏ÌåÖ */
.ham_icon{
    color: var(--ft-navy-color);
    font-weight: 700;
}
/* Î≤ÑÌäº Î∞∞Í≤Ω Î∞ïÏä§ ÏÑ∏ÌåÖ */
.button {
    padding: 10px 10px;
    display: block;
    background-color: var(--ft-lemon-color);
    border-radius: 50%;
    
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    
    transition: all 0.1s ease-out;
}
.button:active{
    transform: scale(0.9);
}

/************************ Î©îÎâ¥ Î∞ïÏä§ ÎîîÏûêÏù∏ ÏÑ∏ÌåÖ ************************/

/* ÎÇ¥ÎπÑ Í≤âÎ∞ïÏä§ */
#nav{
    width: 100%;
    height: 100%;

    position: fixed;
    z-index: 3;

    background-color: var(--bg-navy-color);
    
    /* ÌÅ¥Î¶ΩÌå®Ïä§Î°ú Î≥¥ÏòÄÎã§ ÏïàÎ≥¥ÏòÄÎã§ Ï°∞Ï†àÌïòÍ∏∞ Îß®ÏïûÏùò ÌçºÏÑºÌã∞ÏßÄÍ∞Ä ÏõêÌòï ÌÅ¨Í∏∞ Ï°∞Ï†àÌïòÎäî Í≤ÉÏù¥Í≥† Îí§Ïùò 2Í∞úÍ∞Ä x,yÍ∞í Ï°∞Ï†àÏûÑ */
    clip-path: circle(0% at 86.2% 60px);
    /* display: none; */
    /* clip-path: none; */
    
    /* border: 2px dashed blueviolet; */
}

/* üî•Î∞©Î≤ï1) ÎÇ¥ÎπÑÍ≤åÏù¥ÏÖò Î∞ïÏä§ Ïï†ÎãàÎ©îÏù¥ÏÖò ÌÇ§ÌîÑÎ†àÏûÑ */
@keyframes navOpenAni {
    0%{
        clip-path: circle(0% at 86.2% 60px);
    }
    100%{
        clip-path: circle(150% at 86.2% 60px);
    }
}
@keyframes navCloseAni {
    0%{
        clip-path: circle(150% at 86.2% 60px);
    }
    100%{
        clip-path: circle(0% at 86.2% 60px);
    }
}

/* ÎÇ¥ÎπÑÏóê ÌÅ¥ÎûòÏä§ "on"Ïù¥ Î∂ÄÏó¨ÎêòÎ©¥ Í∞ÄÏÉÅÏöîÏÜåÎ°ú ÎçÆÍ∏∞! */
.nav.on::after{
    content: '';
    position: absolute;
    top: 50px;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;

}

/* Î©îÎâ¥ Í≤âÎ∞ïÏä§ */
#nav>.wrap{
    position: relative;
    max-width: 1400px;
    padding: 0 30px;
    margin: 20px auto 0;

    /* outline: 2px dashed blue; */
}

/* Î©îÎâ¥ Î∞ïÏä§ */
.nav{
    /* position: fixed;
    left: 50%;
    transform: translateX(-50%);
    max-width: 1400px; */

    /* outline: 1px dashed red; */
}

/* Î©îÎâ¥ aÏöîÏÜå Í≥µÌÜµ ÏÑ∏ÌåÖ */
.nav a{
    font-family: 'Cormorant Garamond', serif;
    font-size: 30px;
    font-weight: bold;
    color: #faf8f5;
    letter-spacing: 2px;
}
/* Îã´Í∏∞ Î≤ÑÌäº */
.nav>.wrap:has(.close){
    height: 80px;
    padding-right: 10px;
    display: flex;
    flex-direction: row-reverse;
    align-items: center;

    /* outline: 2px solid red; */
}
.nav .close{
    display: block;
    /* padding: 12px 30px; */

    font-size: 24px;
    color: #faf8f5;

    cursor: pointer;
}
.nav>ul{
    display: flex;
    flex-direction: column;
    gap: 5vh;
    padding: 20px;
}

