/* Ï†úÌíà ÎßÅÌÅ¨ Î™®Îìà - Îã§Ïùå Ï†úÌíàÏúºÎ°úÏùò ÎßÅÌÅ¨ CSS - nextLink.css */


/* Ï†úÏùº Í≤âÎ∞ïÏä§ */
.next_link_container{
    position: relative;
    /* width: 100vw; */
    height: 100vh;
    margin-bottom: 50px;

    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    
    /* border: 2px dashed red; */
}
/* ÎßÅÌÅ¨ ÏöîÏÜå */
.next_link_container a{
    display: flex;
    justify-content: center;

    /* border: 2px dashed yellowgreen; */
}

/* Ïù¥Î¶Ñ(h2) Í≤âÎ∞ïÏä§ */
.next_link_container .wrap:has(.name){
    position: relative;
    z-index: 1;
    top: -20%;
}

/* Ïù¥Î¶Ñ(h2) */
.next_link_container .name{
    text-align: center;

    font-family: 'Cormorant Garamond', serif;
    font-size: max(95px, 8vmax);
    color: var(--ft-navy-color);

    /* border: 2px dashed black; */
}
/* Í∞ïÏ°∞ ÌëúÏãú */
.next_link_container i.hit{
    font-style: normal;
    color: var(--ft-lemon-color);
}
.next_link_container i.rose{
    font-style: normal;
    color: var(--ft-rose-color);
}

/* imgÏöîÏÜå Í≤âÎ∞ïÏä§ */
.next_link_container .wrap:has(img){
    position: absolute;
    bottom: 0;
    /* width: 500px; */
    /* height: 700px; */
    overflow: hidden;

    /* width: calc(100vw - 70%); */
    aspect-ratio: 450/600;

    /* border: 2px dashed blue; */
}
/* Ïù¥ÎØ∏ÏßÄÏöîÏÜå */
.next_link_container img{
    width: 100%;
    /* height: 100%; */
    object-fit: cover;
    /* object-position: top; */
    /* outline: 3px dashed #000; */
}
/* Ïù¥ÎØ∏ÏßÄ ÏöîÏÜåÏùò Í∞ÄÏÉÅÏöîÏÜå : ÌÅ¥Î¶≠ÌïòÍ∏∞ ÏúÑÌï®? */
.next_link_container img{
    
}


/* ÎØ∏ÎîîÏñ¥ÏøºÎ¶¨ 1400px Ïù¥Ìïò ÏãúÏûë */
@media screen and (max-width:1400px) {
    /* imgÏöîÏÜå Í≤âÎ∞ïÏä§ */
    .next_link_container .wrap:has(img){
        width: 500px;
    }
} /* ÎØ∏ÎîîÏñ¥ÏøºÎ¶¨ 1400px Ïù¥Ìïò ÎÅù */

/* ÎØ∏ÎîîÏñ¥ÏøºÎ¶¨ 1000px Ïù¥Ìïò ÏãúÏûë */
@media screen and (max-width:1000px) {
    /* imgÏöîÏÜå Í≤âÎ∞ïÏä§ */
    .next_link_container .wrap:has(img){
