/* ÎùºÏù∏ svg Í≤âÎ∞ïÏä§ */
.line_bx{
    display: flex;
    justify-content: center;
    align-items: center;
}
/* ÌôîÏÇ¥Ìëú Íº¨Î¶¨ svg Í≥µÌÜµ ÏÑ∏ÌåÖ */
.line_bx .arrow-tail{
    width: 20px;
    height: 20px;
}
/* ÎùºÏù∏ Ï§ë Í∞ÄÏö¥Îç∞ Ïã§ÏÑ† svg ÏÑ§Ï†ï */
.line_bx .border{
    width: 70vw;
    height: 20px;
    margin: 0 10px;
}
/* ÌôîÏÇ¥Ìëú Íº¨Î¶¨ÏôÄ Í∞ÄÏö¥Îç∞ Ïã§ÏÑ† ÏÇ¨Ïù¥ ÏõêÏùÑ ÏúÑÌïú Î∞ïÏä§ */
.line_bx .cir{
    display: flex;
    align-items: center;
}
.line_bx .cir::before{
    content: '';
    display: block;
    width: 3px;
    height: 3px;
    border-radius: 50%;
    background-color: var(--arrow-grey-color);
    margin-left: 10px;
}
.line_bx .cir::after{
    content: '';
    display: block;
    width: 3px;
    height: 3px;
    border-radius: 50%;
    background-color: var(--arrow-grey-color);
    margin-right: 10px;
}

/* ÎùºÏù∏ Ï§ë Íº¨Î¶¨ Î™®Ïñë(.right) ÏÑ§Ï†ï */
.line_bx svg:nth-child(3){
    transform: rotateY(180deg)
}

