/* ÌïòÎã® ÏòÅÏó≠ ÏúÑÏ™ΩÏùò Í≤ΩÍ≥ÑÏÑ† svg ÏöîÏÜåÎì§ ÏÑ∏ÌåÖ */
#Group-1{
    stroke:none;
