@charset "utf-8";
/* Í∞Å Ï†úÌíà ÏÜåÍ∞ú Î™®Îìà CSS - details.css */


/* Ï†úÏùº Í≤â Î∞ïÏä§ */
#details{
    background-color: var(--btn-lemon-color);
}
.details{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    margin-bottom: 100px;

}


/* Ï†úÌíàÎ™Ö Î∞ïÏä§ ÏÑ∏ÌåÖ */
.details .product_name_container{
    padding: 100px 20px 0;
    margin-bottom: 50px;
}
/* Ï†úÌíàÎ™Ö Ìè∞Ìä∏Í¥ÄÎ†® ÏÑ∏ÌåÖ */
.details .product_name_container .name{
    font-family: 'Cormorant Garamond', serif;
    font-size: 7vmax;
    color: var(--ft-navy-color);
}
/* Ï†úÌíàÎ™Ö(Îã®Ïñ¥) ÏúÑÏπò ÏÑ∏ÌåÖ */
.details .product_name_container .name>span{
    display: flex;
    gap: 20px;
}
/* Ï†úÌíàÎ™Ö(Îí§Ïóê Ïò§Îäî Îã®Ïñ¥) Í∞úÎ≥Ñ ÏÑ∏ÌåÖ */
.details .product_name_container .name .letter:nth-child(2){
    font-style: italic;
    color: var(--ft-lemon-color);
}
.details .product_name_container .name .letter.rouge:nth-child(2){
    color: var(--ft-rose-color);
}

/* Í∑∏Î¶¨Îìú Î∞ïÏä§ ÏÑ∏ÌåÖ */
.details .gridBx{
    max-width: 1400px;
    display: grid;
    /* Í∞ÄÎ°ú Í∑∏Î¶¨Îìú ÌÖúÌîåÎ¶ø */
    grid-template-columns: repeat(12, 1fr);
    grid-column-gap: 20px;

    padding: 20px 20px 0;
    margin-bottom: 50px;

    /* outline: 1px dashed black; */
}
.details .product_img_container{
    grid-column: 2/7;
}
.details .product_desc_container{
    grid-column: 7/12;
}



/* Ï†úÌíà Ïú†Î¶¨Î≥ë img ÏöîÏÜå ÏÑ∏ÌåÖ */
.details .product_img_container img#product{
    width: 100%;
    margin-top: 30vh;

    /* outline: 1px dashed red; */
}

